@import "style/variable.scss";

.book {
    width: 100vw;
    padding: $layout_padding;

    display: flex;
    gap: 20px;

    &__profile {
        width: 60%;
        height: auto;
        min-height: 100vh;

        margin-bottom: 20px;
        padding: 10px 12px;

        background-color: $white_color;
        border-radius: $border_intensity;
    }

    &__roadmap {
        width: 40%;
        height: 610px;
        background-color: $white_color;
        border-radius: $border_intensity;
        overflow: scroll;
        position: sticky;
        top: 80px;
    }
}

.book__profile__info {
    width: 100%;
    height: 230px;
    display: flex;

    &__vertical {
        width: 180px;
        height: 100%;

        &__image {
            width: 180px;
            height: 180px;
            border-radius: 50%;
            background-color: pink;
            overflow: hidden;

            & > img {
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }

        &__follow {
            width: 100%;
            height: 50px;
            padding: 4% 6%;

            display: flex;
            flex-direction: row;
            justify-content: space-evenly;
            align-items: center;

            // background-color: green;
        }
    }

    &__summary {
        width: 100%;
        height: 100%;

        display: flex;
        flex-wrap: wrap;
        justify-content: space-evenly;
        align-items: center;

        padding-bottom: 10px;

        &__element {
            width: 40%;
            height: 20%;

            display: flex;
            flex-direction: column;

            &__title {
                font-weight: 500;
                color: $bold_color;
            }

            &__body {
                font-size: 0.875rem;
                font-weight: 100;
            }
        }
    }
}

.book__profile__info__vertical__follow {
    & > button {
        all: unset;
        height: 22px;

        padding: 4% 4%;
        border-radius: $border_intensity;

        display: flex;
        justify-content: center;
        align-items: center;

        transition: background-color 0.3s, color 0.3s;

        &:hover {
            background-color: $background_color;
        }
        &:active {
            background-color: $bold_color;
            color: $white_color;
        }
    }

    &__heart {
        width: 35px;
        height: 35px;

        display: flex;
        justify-content: center;
        align-items: center;
        border-radius: 50%;

        transition: background-color 0.3s;
        &:hover {
            background-color: $background_color;
        }
    }
}

.book__profile__question {
    width: 100%;
    height: auto;

    display: flex;
    flex-direction: column;
    gap: 12px;
    
    & a {
        all: unset;
    }

    &__write {
        color: black;
        width: 100%;
        height: 50px;
        padding: 10px 12px;

        display: flex;
        justify-content: center;
        align-items: center;
        
        border-radius: $border_intensity;
        background-color: $background_color;

        transition: background-color 0.3s, color 0.3s, box-shadow 0.3s;

        &:hover {
            // background-color: $emph_color;
            box-shadow: $normal_shadow;
        }

        &:active {
            background-color: $bold_color;
            color: $white_color;
        }
    }

    &__summary {
        width: 100%;
        height: 80px;
        padding: 10px 12px;

        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: flex-start;

        border-radius: $border_intensity;
        background-color: $background_color;

        transition: background-color 0.3s, color 0.3s, box-shadow 0.3s;

        &:hover {
            // background-color: $emph_color;
            box-shadow: $normal_shadow;
        }

        &:active {
            background-color: $bold_color;
            color: $white_color;
        }

        &__question {
            width: 100%;
            font-size: 1rem;
        }

        &__answer {
            font-size: 0.8rem;
            font-weight: 100;
        }
    }
}

.book__roadmap__detail {
    display: flex;
    justify-content: space-evenly;
    margin-top: 12px;

    &__month {
        display: flex;
        flex-direction: column;

        margin-top: 7px;
        color: $bold_color;

        & p:first-child {
            font-size: 0.7rem;
            color: black;
            opacity: 0.5;
        }
    }

    &__icon {
        position: relative;

        display: flex;
        justify-content: center;

        &__circle {
            width: 40px;
            height: 40px;
            border-radius: 50%;
            border: solid 6px $background_color;
            background-color: $emph_color;
        }

        &__line {
            position: absolute;
            z-index: -1;
            width: 10px;
            height: 100%;
            margin-top: 14px;
            background-color: $background_color;
        }
    }

    &__summary {
        width: 64%;
        padding: 10px 12px;
        border-radius: 12px;

        color: $bold_color;
        background-color: $background_color;

        &__title {
            height: 2rem;
            font-weight: bold;
            border-bottom: solid 1px $emph_color;
        }

        &__content {
            margin-top: 0.5rem;
            font-size: 0.9rem;
        }
    }

    &:nth-child(even) .book__roadmap__detail__summary {
        color: $white_color;
        background-color: $emph_color;

        &__title {
            border-bottom: solid 1px rgba(255, 255, 255, 0.5);
        }
    }
}
